body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.app {
  color: white;
  background: #1c1c1c;
  width: 100%; }
  .app .topBar {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 4; }
    .app .topBar .menu {
      float: right;
      margin-right: 50px; }
      @media screen and (max-width: 600px) {
        .app .topBar .menu {
          width: 100%;
          float: none;
          margin: 0; } }

.mainPage .topTitle {
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 3;
  overflow: hidden; }
  .mainPage .topTitle .middleCard {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    pointer-events: none; }
    .mainPage .topTitle .middleCard .title h1 {
      font-family: "Black Ops One", cursive;
      font-weight: normal !important;
      font-size: 10vw;
      margin: 2px;
      color: #ff1a59;
      text-shadow: 1px 1px 0 black, 2px 2px 0 black, 3px 3px 0 black, 4px 4px 0 black, 5px 5px 0 #0aeff0, 6px 6px 0 #0aeff0, 7px 7px 0 #0aeff0, 8px 8px 0 #0aeff0, 9px 9px 0 #0aeff0, 10px 10px 0 #0aeff0, 11px 11px 0 #0aeff0;
      line-height: 9vw; }
      @media screen and (max-width: 600px) {
        .mainPage .topTitle .middleCard .title h1 {
          font-size: 17vw;
          line-height: 16vw; } }
      @media screen and (max-width: 800px) {
        .mainPage .topTitle .middleCard .title h1 {
          font-size: 15vw;
          line-height: 14vw; } }

.mainPage .projectList {
  box-sizing: border-box;
  background: #1c1c1c;
  position: relative;
  padding-top: 4rem; }
  .mainPage .projectList::before {
    position: absolute;
    top: 0;
    height: 4rem;
    width: 100%;
    content: "";
    background: repeating-linear-gradient(315deg, #0aeff0 0px, #0aeff0 25px, #1c1c1c 0px, #1c1c1c 50px, #ff1a59 0px, #ff1a59 75px, #1c1c1c 0px, #1c1c1c 100px); }
  .mainPage .projectList .projectListInner {
    padding: 2rem; }

.listProject {
  display: flex;
  flex-direction: column;
  align-items: center; }

.lds-dual-ring {
  display: inline-block;
  width: 19px;
  height: 19px;
  padding-left: 5px; }

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 7px;
  height: 7px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite; }

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.projectCardWrapper {
  width: 700px;
  height: 220px;
  position: relative; }
  .projectCardWrapper .projectCard {
    width: 700px;
    height: 220px;
    background: #414141;
    box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
    -webkit-transition: 0.5s;
    transition: 0.5s;
    border-radius: 3px;
    right: 0px;
    top: 0;
    position: relative;
    display: block;
    text-decoration: none;
    color: white; }
    .projectCardWrapper .projectCard:hover {
      top: -5px;
      right: 10px;
      box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2); }
    .projectCardWrapper .projectCard .cardHeader {
      display: flex;
      flex-direction: row;
      align-items: center; }
      .projectCardWrapper .projectCard .cardHeader .icon {
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        height: 220px; }
        .projectCardWrapper .projectCard .cardHeader .icon img {
          height: 170px;
          width: 170px;
          object-fit: cover;
          margin-left: -20px;
          box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
          border-radius: 3px; }
        .projectCardWrapper .projectCard .cardHeader .icon .noIcon {
          height: 170px;
          width: 170px;
          box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
          border-radius: 3px;
          margin-left: -20px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center; }
      .projectCardWrapper .projectCard .cardHeader .cardTitle {
        flex-grow: 1;
        flex-shrink: 1;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-width: 0; }
        .projectCardWrapper .projectCard .cardHeader .cardTitle .titleWrapper {
          margin: 10px 0;
          display: flex;
          flex-direction: row;
          align-items: center; }
          .projectCardWrapper .projectCard .cardHeader .cardTitle .titleWrapper .title {
            display: block;
            margin: 0; }
          .projectCardWrapper .projectCard .cardHeader .cardTitle .titleWrapper .githubLink {
            display: block;
            padding-right: 10px; }
        .projectCardWrapper .projectCard .cardHeader .cardTitle .subtitle {
          margin: 0; }
        .projectCardWrapper .projectCard .cardHeader .cardTitle .techs {
          overflow: hidden;
          margin-top: 10px; }
          .projectCardWrapper .projectCard .cardHeader .cardTitle .techs .techList {
            height: 35px;
            white-space: nowrap; }
      .projectCardWrapper .projectCard .cardHeader .onlineProject {
        border-radius: 3px;
        flex-shrink: 0;
        flex-grow: 0;
        padding: 5px;
        height: 20px;
        margin-right: 20px;
        background: #0aeff0;
        color: #191919;
        box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2); }
        .projectCardWrapper .projectCard .cardHeader .onlineProject.down {
          background: red; }
        .projectCardWrapper .projectCard .cardHeader .onlineProject.maybe {
          background: yellow; }
        .projectCardWrapper .projectCard .cardHeader .onlineProject.loading {
          display: flex;
          flex-direction: row;
          justify-content: center;
          line-height: 20px; }
  .projectCardWrapper:not(:first-child) {
    margin-top: 2rem; }


